<template>
    <div class="demo">
        <div id="computed-basics">
        <p>Has published books:</p>
        <span>{{ author.books.length > 0 ? 'Yes' : 'No' }}</span>
        <div v-text="codeVhtml" class="codeText"></div>
        <span>{{ publishedBooksMessage }}</span>
        <div v-text="codeVcomputed" class="codeText"></div>
        <span>{{ calculateBooksMessage() }}</span>
        <div v-text="codeVmethods" class="codeText"></div>
        </div>
        <p class="text-fix">
            
            listlistlistgettermethod
        </p>
        <p class="text-fix">
            Vuewatch 
        </p>
        <p style="color: gray">
            https://yesno.wtf/api  {"answer":"no","forced":false,"image":"https://yesno.wtf/assets/no/23-5fe6c1ca6c78e7bf9a7cf43e406fb8db.gif"}
        </p>
        <div id="watch-example">
            <p>
                Ask a yes/no question:
                <input v-model="question" />
            </p>
            <p>{{ answer }}</p>
        </div>
        <div id="demo">{{ fullName }}</div>
    </div>
</template>
<script>
export default {
  name: 'ComputedProp',
  data () {
    return {
      author: {
        name: 'John Doe',
        books: [
          'Vue 2 - Advanced Guide',
          'Vue 3 - Basic Guide',
          'Vue 4 - The Mystery'
        ]
      },
      question: '',
      answer: 'Questions usually contain a question mark. )',
      firstName: 'Foo',
      lastName: 'Bar',
      // fullName: 'Foo Bar',
      codeVhtml: '<span>{{ author.books.length > 0 ? \'Yes\' : \'No\' }}</span>',
      codeVcomputed: 'computedpublishedBooksMessage() {return this.author.books.length > 0 ? \'Yes\' : \'No\'}, html<span>{{ publishedBooksMessage }}</span>',
      codeVmethods: 'methodscalculateBooksMessage() {return this.author.books.length > 0 ? \'Yes\' : \'No\'}, html<span>{{ calculateBooksMessage() }}</span>'
    }
  },
  watch: {
    // whenever question changes, this function will run
    question (newQuestion, oldQuestion) {
      if (newQuestion.indexOf('?') > -1) {
        this.getAnswer()
      }
    }
  },
  computed: {
    publishedBooksMessage () {
      return this.author.books.length > 0 ? 'Yes' : 'No'
    },
    now () {
      return Date.now()
    },
    fullName () {
      return 'computed' + this.firstName + ' ' + this.lastName
    }
  },
  methods: {
    calculateBooksMessage () {
      return this.author.books.length > 0 ? 'Yes' : 'No'
    },
    getAnswer () {
      // axios index.html
      this.answer = 'Thinking...'
      // eslint-disable-next-line no-undef
      axios
        .get('https://yesno.wtf/api')
        .then(response => {
          this.answer = response.data.answer
        })
        .catch(error => {
          this.answer = 'Error! Could not reach the API. ' + error
        })
    }
    // firstName(val) {
    //     this.fullName = val + ' ' + this.lastName
    // },
    // lastName(val) {
    //     this.fullName = this.firstName + ' ' + val
    // }
  }
}
</script>
